.infoContent {
    background-image: url("../../../assets/content .jpg");
    height: 300px;
    border-radius: 8px;
    background-repeat: no-repeat;
    color: white;
    display: flex;
    align-items: center;
    padding-left: 50px;
    font-size: 20px;
}
.infoContent .avatar {
    display: flex;
    flex-direction: column;
}

.infoContent .avatar > img {
    width: 140px;
    height: 140px;
    border-radius: 50%;
    margin-bottom: 20px;
}
.description{
    margin-left:50px ;
    display: flex;
    flex-direction: column;
}
.name{
    font-weight: 700;
    margin-bottom: 20px;
}